.test-border {
    border: 1px solid red;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    width: 10px;
    border: rgb(189, 233, 243) 1px solid;
    border-radius: 5px;
    background-color: rgb(189, 233, 243);
}

html, body {
    height: 100%;
    width: 100%;
}

#root {
    height: 100%;
    width: 100%;
    background-color: rgb(96, 109, 255);
}

.message-gallery {
    background-color: white;
    width: 60%;
    height: 80%;
    border: outset;
}

.recipient-list {
    max-height: 100%;
}

.message-room {
    max-height: 100%;
}

.h-10-percent {
    height: 10%;
}

.h-80-percent-oflow {
    height: 80%;
    overflow-y: scroll;
}


.input-prompt {
    border: 1px solid #198754;
    outline: none;
}

.border-curve-10 {
    border-radius: 10px;
}


@media only screen and (max-width: 600px) {
    .message-gallery {
        background-color: white;
        width: 98%;
        height: 80%;
        border: outset;
    }
}